        onKeyPress(button) {
            console.log("Button pressed", button);
            switch (button) {
                case '{default}':
                    this.keyboard.setOptions({
                        layoutName: 'default'
                    });
                    break;
                case '{gestures}':
                    this.keyboard.setOptions({
                        layoutName: 'gestures'
                    });
                    break;
